<template>
  <div v-zinx="{ page: 'limit' }" class="page limit">
    <div class="wrapper-mask"></div>
    <div class="limit_show">
      <div class="limit_kefu">
        <div class="cursor_point limit-button-wrapper">
          <div v-zoom class="limit_kefu_btn c_box" @click="onJump('icon')">
            <icon-font name="icon-Service_email" :size="commonStyle('limit_kefu_icon_size')" />
          </div>
          <span class="limit_kefu__label">Service</span>
        </div>
      </div>
      <div class="limit_content column_flex">
        <img class="limit_content_logo" :src="$loadIcon('logo_lg')" alt="" />
        <div class="limit_text">
          <div class="line">
            <!-- <icon-font
							class="line-icon"
							name="icon-icon_IP_ban"
							:size="commonStyle('limit_content_icon_size')"
						/> -->
            <img class="line-icon" src="@image/limitation/disabled.png" alt="" />
            <div class="line__bottom">
              <span class="line__title">{{ $t('UI_limit') }}</span>
              <span class="line__content">
                {{ $t('UI_limit_text') }}
              </span>
            </div>
          </div>
          <div class="line">
            <!-- <icon-font
							class="line-icon"
							name="icon-icon_IP_region"
							:size="commonStyle('limit_content_icon_size')"
						/> -->
            <img class="line-icon" src="@image/limitation/map.png" alt="" />
            <div class="line__bottom">
              <span class="line__title">{{ $t('UI_limit_region') }}</span>
              <span class="line__content">
                {{ $t('UI_limit_region_text') }}
              </span>
            </div>
          </div>
        </div>
        <div class="limit_footer column_flex">
          <v-btn
            class="btn btn_radius"
            color="t_primary_dark_color"
            :height="commonStyle(isApp ? 'limit_footer_btn_app_height' : 'limit_footer_btn_pc_height')"
            block
            @click="onJump('btn')"
          >
            {{ $t('UI_limit_btn') }}
          </v-btn>
        </div>
        <div class="ip-info">
          <div class="ip-row">IP:{{ systemConfig.ip }}</div>
          <div class="ip-row">IP address: {{ systemConfig.ip_area }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { VUE_APP_KF_LINK } from '@/api/setting.js'
import _common from '@style/views/limitation.modules.scss'

export default {
  name: 'limit',
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig'])
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 跳转客服
    onJump(sign = 'icon') {
      if (sign == 'icon') return (window.location.href = VUE_APP_KF_LINK || '')

      window.location.href = this.systemConfig.kf_url || ''
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/limitation.modules.scss';

// 针对移动端样式
.wrapper-mask {
  background: $limit_wrapper_bg_pc;
}
.limit_show {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $limit_bg_app;
  overflow-y: auto;
  padding-bottom: 24px;

  .limit_kefu {
    @include setBox(row, flex-end, center);
    padding: $limit_kefu_app_padding;

    .limit-button-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;

      .limit_kefu_btn {
        width: $limit_kefu_width;
        height: $limit_kefu_height;
        background: $limit_kefu_bg;
        border-radius: $limit_kefu_radius;
        box-shadow: $limit_kefu_btn_shadow;
        border: $limit_kefu_btn_border;
      }

      .limit_kefu__label {
        font-size: $limit_base_app_font_size;
        line-height: 1.7;
      }
    }
  }
  .limit_content {
    margin: $limit_content_margin;
    .limit_content_logo {
      width: $limit_content_app_logo_width;
    }
    .limit_text {
      margin: $limit_content_app_text_margin;
      padding: $limit_content_app_text_padding;
      .line {
        background-color: $limit_block_bg;
        border-radius: $limit_content_line_radius;
        padding: $limit_content_line_padding;
        text-align: center;
        &:last-child {
          margin: $limit_content_line_margin;
        }
        .line-icon {
          display: block;
          margin: 0 auto;
          width: $limit_content_icon_size;
          height: auto;
        }
        .line__title {
          display: block;
          margin: $limit_content_title_margin;
          color: $limit_text_title;
          font-size: $limit_base_app_font_size;
          font-weight: $limit_content_title_font_weight;
        }
        .line__content {
          display: block;
          font-size: $limit_content_content_font_size;
          font-weight: $limit_content_content_font_weight;
          color: $limit_text_content;
          margin: $limit_content_content_app_margin;
          line-height: 1.2;
        }
      }
    }
  }
  .limit_footer {
    width: $limit_footer_app_width;
    margin: $limit_footer_app_margin;
    .btn {
      font-size: $limit_footer_btn_app_font_size !important;
      font-weight: $limit_footer_btn_app_font_weight !important;
    }
    .btn_radius {
      border-radius: $limit_footer_btn_radius !important;
    }
  }
  .ip-info {
    margin: $limit_ip_margin;
    text-align: center;
    font-size: $limit_ip_font_size;
    color: $limit_text_content;
    line-height: 1;

    .ip-row + .ip-row {
      margin-top: $limit_ip_row_gap;
    }
  }
}

// 针对pc样式
@include breakpoint-only-up($px: $limit_pc_width) {
  .limit_show {
    width: $limit_pc_width;
    height: $limit_pc_height;
    left: 50%;
    top: 50%;
    bottom: auto;
    right: auto;
    transform: translate(-50%, -50%);
    background: $limit_bg_pc;
    border-radius: $limit_pc_radius;
    .limit_kefu {
      padding: $limit_kefu_pc_padding;
    }
    .limit_content {
      margin: $limit_content_pc_margin;
      .limit_content_logo {
        width: $limit_content_pc_logo_width;
      }
      .limit_text {
        margin: $limit_content_pc_text_margin;
        padding: $limit_content_pc_text_padding;
        .line {
          padding: $limit_content_line_pc_padding;

          .line__title {
            font-size: $limit_base_pc_font_size;
          }

          .line__content {
            font-size: $limit_content_content_pc_font_size;
          }
        }
      }
    }
    .limit_footer {
      width: $limit_footer_pc_width;
      margin: $limit_footer_pc_margin;
      .btn {
        font-size: $limit_base_app_font_size !important;
        font-weight: $limit_footer_btn_pc_font_weight !important;
      }
    }
    .ip-info {
      margin: $limit_ip_pc_margin;
      font-size: $limit_content_content_pc_font_size;
    }
  }
}
</style>
